.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: top;
  font-size: calc(20px);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.game {
    height: 60vh;
    width: 60vw;
    background-color: pink;
    text-align: left;
    position: relative;
}

.button {
    height: 25px;
    width: 100px;
}

.buttonDiv {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
}

.circle {
    height: 25px;
    width: 25px;
    background-color: green;
    border-radius: 50%;
    position: absolute;
    z-index:10;
}

.score {
    font-size: 14px;
    list-style: none;
    padding-left: 0;
}

.scoreTitle {
    font-size: 16px;
}
